<template>
  <div class="admin-meeting-manager">
    <div class="manager-header">
      <h2>‰æã‰ºöÁÆ°ÁêÜ</h2>
      <div class="header-actions">
        <BaseButton 
          variant="primary"
          @click="handleCreate"
        >
          + Êñ∞Âª∫‰æã‰ºö
        </BaseButton>
        <BaseButton 
          variant="secondary"
          @click="handleRefresh"
        >
          Âà∑Êñ∞
        </BaseButton>
      </div>
    </div>

    <!-- Á≠õÈÄâÂô® -->
    <div class="filter-section">
      <MeetingFilter 
        v-model:filters="filters"
        @change="handleFilterChange"
      />
    </div>

    <!-- ÁªüËÆ°Âç°Áâá -->
    <div class="stats-section">
      <div class="stats-grid">
        <BaseCard class="stat-card">
          <div class="stat-content">
            <div class="stat-icon meeting-icon">
              üìÖ
            </div>
            <div class="stat-info">
              <div class="stat-number">
                {{ totalMeetings }}
              </div>
              <div class="stat-label">
                ÊÄª‰æã‰ºöÊï∞
              </div>
            </div>
          </div>
        </BaseCard>

        <BaseCard class="stat-card">
          <div class="stat-content">
            <div class="stat-icon recent-icon">
              üÜï
            </div>
            <div class="stat-info">
              <div class="stat-number">
                {{ recentMeetings }}
              </div>
              <div class="stat-label">
                Êú¨Âë®‰æã‰ºö
              </div>
            </div>
          </div>
        </BaseCard>

        <BaseCard class="stat-card">
          <div class="stat-content">
            <div class="stat-icon participants-icon">
              üë•
            </div>
            <div class="stat-info">
              <div class="stat-number">
                {{ totalParticipants }}
              </div>
              <div class="stat-label">
                ÂèÇ‰∏é‰∫∫Ê¨°
              </div>
            </div>
          </div>
        </BaseCard>

        <BaseCard class="stat-card">
          <div class="stat-content">
            <div class="stat-icon action-icon">
              ‚úÖ
            </div>
            <div class="stat-info">
              <div class="stat-number">
                {{ completedActions }}
              </div>
              <div class="stat-label">
                ÂæÖÂäûÂÆåÊàê
              </div>
            </div>
          </div>
        </BaseCard>
      </div>
    </div>

    <!-- ‰æã‰ºöÂàóË°® -->
    <div class="meetings-section">
      <BaseCard class="content-card">
        <template #header>
          <div class="section-header">
            <h3>‰æã‰ºöÂàóË°®</h3>
            <div class="view-controls">
              <button 
                class="view-btn"
                :class="{ active: viewMode === 'grid' }"
                @click="viewMode = 'grid'"
              >
                <GridIcon size="16" />
              </button>
              <button 
                class="view-btn"
                :class="{ active: viewMode === 'list' }"
                @click="viewMode = 'list'"
              >
                <ListIcon size="16" />
              </button>
            </div>
          </div>
        </template>

        <div
          v-if="loading"
          class="loading-container"
        >
          <LoadingSpinner />
          <p>Âä†ËΩΩ‰æã‰ºöÊï∞ÊçÆ‰∏≠...</p>
        </div>

        <div
          v-else-if="filteredMeetings.length === 0"
          class="empty-state"
        >
          <div class="empty-icon">
            üìÖ
          </div>
          <h3>ÊöÇÊó†‰æã‰ºöËÆ∞ÂΩï</h3>
          <p>ÊÇ®ËøòÊ≤°ÊúâÂàõÂª∫‰ªª‰Ωï‰æã‰ºöËÆ∞ÂΩï</p>
          <BaseButton
            variant="primary"
            @click="handleCreate"
          >
            ÂàõÂª∫Á¨¨‰∏Ä‰∏™‰æã‰ºö
          </BaseButton>
        </div>

        <div v-else>
          <!-- ÁΩëÊ†ºËßÜÂõæ -->
          <div
            v-if="viewMode === 'grid'"
            class="meetings-grid"
          >
            <div 
              v-for="meeting in paginatedMeetings" 
              :key="meeting.id"
              class="meeting-grid-item"
            >
              <MeetingCard 
                :meeting="meeting"
                :show-actions="true"
                @view="handleView"
                @edit="handleEdit"
                @delete="handleDelete"
                @duplicate="handleDuplicate"
              />
            </div>
          </div>

          <!-- ÂàóË°®ËßÜÂõæ -->
          <div
            v-else
            class="meetings-list"
          >
            <div 
              v-for="meeting in paginatedMeetings" 
              :key="meeting.id"
              class="meeting-list-item"
            >
              <div class="meeting-list-content">
                <div class="meeting-main-info">
                  <h4>{{ meeting.title }}</h4>
                  <div class="meeting-meta">
                    <span class="meta-item">
                      üìÖ {{ formatDate(meeting.date) }}
                    </span>
                    <span class="meta-item">
                      üïê {{ meeting.time }}
                    </span>
                    <span class="meta-item">
                      üë• {{ meeting.attendees.length }} ‰∫∫ÂèÇ‰∏é
                    </span>
                  </div>
                </div>
                <div class="meeting-actions">
                  <button 
                    class="action-btn view-btn"
                    title="Êü•ÁúãËØ¶ÊÉÖ"
                    @click="handleView(meeting)"
                  >
                    üëÅÔ∏è
                  </button>
                  <button 
                    class="action-btn edit-btn"
                    title="ÁºñËæë"
                    @click="handleEdit(meeting)"
                  >
                    ‚úèÔ∏è
                  </button>
                  <button 
                    class="action-btn duplicate-btn"
                    title="Â§çÂà∂"
                    @click="handleDuplicate(meeting)"
                  >
                    üìã
                  </button>
                  <button 
                    class="action-btn delete-btn"
                    title="Âà†Èô§"
                    @click="handleDelete(meeting)"
                  >
                    üóëÔ∏è
                  </button>
                </div>
              </div>
            </div>
          </div>

          <!-- ÂàÜÈ°µ -->
          <div class="pagination-section">
            <BasePagination 
              :current="pagination.current"
              :page-size="pagination.pageSize"
              :total="pagination.total"
              @change="handlePageChange"
            />
          </div>
        </div>
      </BaseCard>
    </div>

    <!-- ËØ¶ÊÉÖÂºπÁ™ó -->
    <MeetingDetailModal 
      :meeting="detailModal.meeting"
      :is-open="detailModal.show"
      @close="detailModal.show = false"
      @view-files="handleViewFiles"
      @view-recording="handleViewRecording"
    />

    <!-- ÁºñËæë/ÂàõÂª∫ÂºπÁ™ó -->
    <MeetingEditModal 
      v-model:show="editModal.show"
      :meeting="editModal.meeting"
      :is-create="editModal.isCreate"
      @save="handleSave"
      @close="editModal.show = false"
    />

    <!-- Âà†Èô§Á°ÆËÆ§ÂºπÁ™ó -->
    <BaseModal 
      v-model:show="deleteModal.show"
      title="Á°ÆËÆ§Âà†Èô§"
      size="sm"
    >
      <p>Á°ÆÂÆöË¶ÅÂà†Èô§‰æã‰ºö„Äå{{ deleteModal.meeting?.title }}„ÄçÂêóÔºü</p>
      <p class="warning-text">
        Ê≠§Êìç‰Ωú‰∏çÂèØÊí§ÈîÄ„ÄÇ
      </p>

      <template #footer>
        <BaseButton 
          variant="secondary" 
          @click="deleteModal.show = false"
        >
          ÂèñÊ∂à
        </BaseButton>
        <BaseButton 
          variant="danger"
          :loading="deleteModal.loading"
          @click="confirmDelete"
        >
          Âà†Èô§
        </BaseButton>
      </template>
    </BaseModal>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted, watch } from 'vue'
import { useHead } from '@vueuse/head'
import { getMeetings, createMeeting, updateMeeting, deleteMeeting } from '@/api/meeting'
import { 
  BaseButton, 
  BaseCard, 
  BasePagination, 
  BaseModal,
  LoadingSpinner 
} from '@/components/common'
import { GridIcon, ListIcon } from '@/components/icons'
import MeetingFilter from '@/components/meetings/MeetingFilter.vue'
import MeetingCard from '@/components/meetings/MeetingCard.vue'
import MeetingDetailModal from '@/components/meetings/MeetingDetailModal.vue'
import type { Meeting, FilterOptions } from '@/types/entities'
import { onAdminRefresh } from '@/utils/adminEvents'
import { recordAdminOperation } from '@/composables/useAdminLogs'

// ËÆæÁΩÆÈ°µÈù¢ÂÖÉÊï∞ÊçÆ
useHead({
  title: '‰æã‰ºöÁÆ°ÁêÜ - ÁÆ°ÁêÜÂëòÂêéÂè∞',
  meta: [
    { name: 'description', content: 'ÁÆ°ÁêÜ‰æã‰ºöËÆ∞ÂΩï„ÄÅÊü•ÁúãÁªüËÆ°‰ø°ÊÅØ' }
  ]
})

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const meetings = ref<Meeting[]>([])
const loading = ref(false)
const viewMode = ref<'grid' | 'list'>('grid')

const filters = ref<FilterOptions>({
  search: '',
  status: '',
  sortBy: 'date',
  sortOrder: 'desc'
})

const pagination = ref({
  current: 1,
  pageSize: 12,
  total: 0
})

const detailModal = ref({
  show: false,
  meeting: null as Meeting | null
})

const editModal = ref({
  show: false,
  meeting: null as Meeting | null,
  isCreate: false
})

const deleteModal = ref({
  show: false,
  meeting: null as Meeting | null,
  loading: false
})

// ËÆ°ÁÆóÂ±ûÊÄß
const filteredMeetings = computed(() => meetings.value)

const paginatedMeetings = computed(() => meetings.value)

type MeetingLogAction = 'create' | 'update' | 'delete' | 'refresh' | 'duplicate'

const cleanupFns: Array<() => void> = []

const logMeetingAction = (action: MeetingLogAction, message: string, result: 'success' | 'failure' = 'success', targetId?: string | number) => {
  recordAdminOperation({
    entity: 'meetings',
    action,
    message,
    targetId,
    result
  }).catch((error) => {
    console.warn('ËÆ∞ÂΩï‰æã‰ºöÊìç‰ΩúÊó•ÂøóÂ§±Ë¥•:', error)
  })
}

watch(() => pagination.value.current, () => {
  loadMeetings()
})

watch(() => filters.value, () => {
  pagination.value.current = 1
  loadMeetings()
}, { deep: true })

const totalMeetings = computed(() => meetings.value.length)

const recentMeetings = computed(() => {
  const thisWeek = new Date()
  thisWeek.setDate(thisWeek.getDate() - 7)
  return meetings.value.filter(meeting => 
    new Date(meeting.date) >= thisWeek
  ).length
})

const totalParticipants = computed(() => {
  return meetings.value.reduce((total, meeting) => 
    total + meeting.attendees.length, 0
  )
})

const completedActions = computed(() => {
  return meetings.value.reduce((total, meeting) => {
    const actionItems = meeting.actionItems ?? []
    return total + actionItems.filter(item => item.status === 'completed').length
  }, 0)
})

// ÊñπÊ≥ï
const formatDate = (dateStr: string) => {
  return new Date(dateStr).toLocaleDateString('zh-CN')
}

const loadMeetings = async () => {
  loading.value = true
  try {
    const response = await getMeetings({
      page: pagination.value.current - 1, // API uses 0-based indexing
      size: pagination.value.pageSize,
      keyword: filters.value.search,
      // startDate: filters.value.dateRange?.start, // Assuming filters might have dateRange later or we ignore it for now if not in FilterOptions
      // endDate: filters.value.dateRange?.end
    })
    
    meetings.value = response.content
    pagination.value.total = response.totalElements
  } catch (error) {
    console.error('Âä†ËΩΩ‰æã‰ºöÊï∞ÊçÆÂ§±Ë¥•:', error)
  } finally {
    loading.value = false
  }
}

const handleFilterChange = () => {
  pagination.value.current = 1
}

const handlePageChange = (page: number) => {
  pagination.value.current = page
}

const handleCreate = () => {
  editModal.value = {
    show: true,
    meeting: null,
    isCreate: true
  }
}

const handleView = (meeting: Meeting) => {
  detailModal.value = {
    show: true,
    meeting
  }
}

const handleViewFiles = (meeting: Meeting) => {
  const file = meeting.files?.[0]
  if (file?.url) {
    window.open(file.url, '_blank')
  }
}

const handleViewRecording = (meeting: Meeting) => {
  if (meeting.recording) {
    window.open(meeting.recording, '_blank')
  }
}

const handleEdit = (meeting: Meeting) => {
  editModal.value = {
    show: true,
    meeting: { ...meeting },
    isCreate: false
  }
}

const handleDelete = (meeting: Meeting) => {
  deleteModal.value = {
    show: true,
    meeting,
    loading: false
  }
}

const handleDuplicate = (meeting: Meeting) => {
  const duplicatedMeeting = {
    ...meeting,
    id: Date.now().toString(),
    title: `${meeting.title} (ÂâØÊú¨)`,
    date: new Date().toISOString().split('T')[0]
  }
  
  meetings.value.unshift(duplicatedMeeting)
  logMeetingAction('duplicate', `Â§çÂà∂‰æã‰ºö„Äå${meeting.title}„Äç`, 'success', meeting.id)
}

const handleSave = async (meeting: Meeting) => {
  try {
    if (editModal.value.isCreate) {
      const created = await createMeeting(meeting)
      logMeetingAction('create', `ÂàõÂª∫‰æã‰ºö„Äå${created.title}„Äç`, 'success', created.id)
    } else {
      const updated = await updateMeeting(meeting.id, meeting)
      logMeetingAction('update', `Êõ¥Êñ∞‰æã‰ºö„Äå${updated.title}„Äç`, 'success', updated.id)
    }
    await loadMeetings()
    editModal.value.show = false
  } catch (error) {
    console.error('‰øùÂ≠ò‰æã‰ºöÂ§±Ë¥•:', error)
    const action: MeetingLogAction = editModal.value.isCreate ? 'create' : 'update'
    logMeetingAction(action, `‰øùÂ≠ò‰æã‰ºö„Äå${meeting.title}„ÄçÂ§±Ë¥•`, 'failure', meeting.id)
  }
}

const confirmDelete = async () => {
  const target = deleteModal.value.meeting
  if (!target) return
  
  deleteModal.value.loading = true
  
  try {
    await deleteMeeting(target.id)
    await loadMeetings()
    deleteModal.value.show = false
    logMeetingAction('delete', `Âà†Èô§‰æã‰ºö„Äå${target.title}„Äç`, 'success', target.id)
  } catch (error) {
    console.error('Âà†Èô§‰æã‰ºöÂ§±Ë¥•:', error)
    logMeetingAction('delete', `Âà†Èô§‰æã‰ºö„Äå${target.title}„ÄçÂ§±Ë¥•`, 'failure', target.id)
  } finally {
    deleteModal.value.loading = false
  }
}

const handleRefresh = () => {
  loadMeetings()
  logMeetingAction('refresh', 'ÊâãÂä®Âà∑Êñ∞‰æã‰ºöÂàóË°®')
}

onMounted(() => {
  loadMeetings()
  const stopRefreshListener = onAdminRefresh((detail) => {
    if (detail.entity === 'all' || detail.entity === 'meetings') {
      loadMeetings()
      const action = detail.action ?? 'refresh'
      logMeetingAction('refresh', `ÂÖ®Â±ÄËß¶Âèë ${action} ÂêåÊ≠•‰æã‰ºöÊï∞ÊçÆ`)
    }
  })
  cleanupFns.push(stopRefreshListener)
})

onUnmounted(() => {
  cleanupFns.forEach((stop) => stop())
})
</script>

